import React from 'react';
import styles from '../scss/InfoBox.module.scss';
import {Slide} from './Slide';

export class InfoBox extends React.Component {
   state ={
      activeIndex:1,
      slides: []
   };


   async componentDidMount(){
      const response = await fetch(
         'https://my-json-server.typicode.com/crossoover/fake-server/advertisements'
      );
      const slides = await response.json();
      this.setState({slides})
      console.log("DID MOUNT");
   }

   renderSlides(){
      return this.state.slides.map((slide)=>{
         const isActive = this.state.activeIndex === slide.id;

         const someFunction = (text1,text2)=>{
            console.log(text1 + text2);
         }
         return <Slide {...{isActive,...slide, someFunction}}/>;
      })
   }

   firstIndex(){
      this.setState({
         activeIndex: 1
      });
      console.log(this.state.activeIndex);
   }

   decreaseIndex(){
      if (this.state.activeIndex==1){
         this.setState({
            activeIndex: this.state.slides.length
         });
      } else{
         this.setState({
            activeIndex: this.state.activeIndex - 1
         });
      }
      console.log(this.state.activeIndex);
   }

   increaseIndex(){
      if (this.state.activeIndex == this.state.slides.length){
         this.setState({
            activeIndex: 1
         });
      } else{
         this.setState({
            activeIndex: this.state.activeIndex + 1
         });
      }
      console.log(this.state.activeIndex);
   }

   lastIndex(){
      this.setState({
         activeIndex: this.state.slides.length
      });
      console.log(this.state.activeIndex);
   }
   
   render(){
      const slidesJsx = this.renderSlides();
      return (
      <div className={styles.infobox}>
         <div className={styles.slides}>
            {slidesJsx}
         </div>   
         <div className={styles.infobox__controls}>
            <button className={styles.infobox__controls_button} onClick={()=>this.firstIndex()}>{'<<<'}</button>
            <button className={styles.infobox__controls_button} onClick={()=>this.decreaseIndex()}>{'<'}</button>
            <button className={styles.infobox__controls_button} onClick={()=>this.increaseIndex()}>{'>'}</button>
            <button className={styles.infobox__controls_button} onClick={()=>this.lastIndex()}>{'>>>'}</button>
         </div>
      </div>);
   }
}
