import React from 'react';
import styles from '../scss/Slide.module.scss';

export class Slide extends React.Component {
   render(){
      console.log(this.props);
      const {isActive,img,title,description,id,someFunction} = this.props
      return (
         <div key={id} className={`${styles.slide} ${isActive ? styles.active : ''}`}>
            <div className={styles.slide__image}>
               <img className={styles.slide__image_illustration} src={img} alt=""/>
            </div>
            <div className={styles.slide__content}>
               <h2 className={styles.slide__content_heading}>{title}</h2>
               <p className={styles.slide__content_description}>{description}</p>
               <button className={styles.slide__content_toggler} onClick={()=>someFunction('Hello Number ', id)}>Toggle</button>
            </div>
         </div>
      );
   }
}